<template>
    <div class="video-player">
        <video-player class="video-player vjs-custom-skin"
            ref="videoPlayer"
            :playsinline="true"
            :options="playerOptions">
        </video-player>
    </div>
</template>
<script>
export default {
    props:{
        videoSrc:{
            type:String,
            default(){
                return ''
            }
        }
    },
    data(){
        return{
            playerOptions: {
                //
                playbackRates: [0.5, 1.0, 1.5, 2.0], 
                //true,
                autoplay: false, 
                // 
                muted: false, 
                // 
                loop: false, 
                // <video>auto,
                preload: 'auto', 
                language: 'zh-CN',
                //  - "16:9""4:3"
                aspectRatio: '16:9',
                // trueVideo.js player
                fluid: true,
                sources: [{
                    //
                    type: "video/mp4",
                    //url
                    src: '' 
                }],
                //
