<template>
    <div class="work-score-container">
        <el-card style="min-height:600px;">
            
            <el-tabs v-model="activeName">
                <el-tab-pane label="" name="look">
                    <div style="width:900px">
                        <el-row>
                            <el-form ref="form" :model="form" label-width="100px">
                                <el-form-item label="">
                                    <el-col>{{form.activityName}}</el-col>
                                </el-form-item>

                                <el-form-item label="">
                                    <!-- <el-col>{{form.introduction}}</el-col> -->
                                    <div class="content" v-html="form.introduction"></div>
                                </el-form-item>

                                <el-form-item label="">
                                    <el-col><a :href="form.regForm"  target="_blank"></a></el-col>
                                </el-form-item>

                                <el-form-item label="">
                                    <el-col v-for="(item,index) in form.annex" :key="index"><a :href="item.url" target="_blank">{{item.title}}</a><span class="remark">:{{item.remark}}</span></el-col>
                                </el-form-item>    
                            </el-form>
                        </el-row>
                    </div>
                </el-tab-pane>
            </el-tabs>

            <el-tabs v-model="activeDetail">
                <el-tab-pane label="" name="detail">
                    <div style="width:900px">
                        <el-row>
                            <el-form ref="form" :model="form2" label-width="100px">
                                <el-form-item label="">
                                    <el-input
                                    onkeyup="value=value.replace(/[^\d]/g,'')"
                                    placeholder=""
                                    v-model="form2.score"
                                    clearable
                                    style="width:300px;"
                                    size="small">
                                    </el-input>
                                </el-form-item>

                                <el-form-item label="">
                                    <el-input
                                    type="textarea"
                                    :rows="6"
                                    placeholder=""
                                    v-model="form2.introduction">
                                    </el-input>
                                </el-form-item>

                            </el-form>
                        </el-row>
                    </div>
                </el-tab-pane>
            </el-tabs>
            <el-row>
                <el-col :span="2" :offset="11">
                    <el-button type="primary" @click="goToScore(queryId)"></el-button>
                </el-col>
            </el-row>
        </el-card>
    </div>
</template>
<script>
import api from "../../service/api";
import { axiosGet, getCookie } from "../../tools/tools";
import axios from "axios";
export default {
    data(){
        return{
            activeName:'look',
            activeDetail:'detail',
            form: {
                activityName: '',
                introduction:'',
                regForm:[],
                annex:[]
            },
            form2:{
                score:Number(this.$route.query.score),//
                introduction:this.$route.query.comment,//
            },
            apiKey:getCookie("x-api-key"),
            queryId:this.$route.query.id,
        }
    },
    mounted(){

    },
    created(){
        let that=this;
        that.getWorkDetail(that.queryId);
    },
    methods:{
        async getWorkDetail(queryId){
            let that=this;
            let params={};
            params.url=api.enroll;
            params.id=queryId;
            params.inscore=1;
            params.expand="works";
            await this.axiosGet(params).then(res=>{
                that.form.activityName=res.works.title;
                that.form.introduction=res.works.content;
                that.form.annex=res.works.attachment.map(res=>{
                    return{
                        "remark":res.remark,
                        "url":res.url,
                        "title":res.title,
                    }
                })
                that.form.regForm=res.registration;
                
            }).catch(err=>{
                if(err.status===404){
                   that.$message({
                        type: 'error',
                        message: ''
                   });
                   that.queryId=that.queryId-1;
                }
            });

            
        },
        //
        goToScore(queryId){
            let that=this;
            let params={};
            // params.url=api.scoring;
            params.ids=new Array();
            params.ids[0]=queryId;
            params.score=Number(that.form2.score)*10;
            params.comment=that.form2.introduction;
            let params1=new Array();
            params1.push(params);
            // console.log(params1);
            axios.post(api.scoring, 
              params1
            ,{headers: { 'x-api-key': that.apiKey }}).then(res=>{
                // console.log(res);
                if(res.data.code==0){
                    that.open();
                }
            });
        },
        //
        open() {
            let that=this;
            that.$confirm('', '', {
                confirmButtonText: '',
                type: 'warning',
                center: true,
                showCancelButton:false,
            }).then(() => {
                that.queryId=that.queryId-1;
                that.getWorkDetail(that.queryId);

                // this.$message({
                //     type: 'success',
                //      message: ''
                // });
            }).catch((err) => {
                console.log(err)
                // this.$message({
                //     type: 'info',
                //     message: ''
                // });
            });
        }
    }
}
</script>
<style lang="less" scoped>
.work-score-container{
    width:1180px;
    margin:auto;
    margin-top:20px;
}
.remark{
    font-size:14px;
    font-weight:400;
    margin-left:20px;
    color:rgba(153,153,153,1);
}
</style>
