<template>
  <div class="container">
    <el-row>
      <type-select
        :gradeList="gradeList"
        :activityProjectList="activityProjectList"
        :activityTypleList="activityTypleList"
        :regionList="regionList"
        @gradeObject="gradeObject"
        @activityProjectObject="activityProjectObject"
        @activityTypleObject="activityTypleObject"
        @regionObject="regionObject"
      ></type-select>
    </el-row>
    <el-form ref="form" :model="form" :inline="true">
      <el-row>
        <el-col :span="6">
          <el-form-item label="">
            <el-input v-model="form.school" placeholder="" size="small" style="width:160px"></el-input>
          </el-form-item>
        </el-col>

        <el-col :span="4">
          <el-form-item label="">
            <el-input v-model="form.author" placeholder="" size="small" style="width:120px"></el-input>
          </el-form-item>
        </el-col>

        <el-col :span="6">
          <el-form-item label="">
            <el-input v-model="form.mentor" placeholder="" size="small" style="width:160px"></el-input>
          </el-form-item>
        </el-col>

        <el-col :span="6">
          <el-form-item label="">
            <el-input
              v-model="form.work_title"
              placeholder=""
              size="small"
              style="width:160px"
            ></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="2">
          <el-form-item>
            <el-button type="primary" @click="goToSearch()" size="small" icon="el-icon-search"></el-button>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <!---->
    <el-row>
      <el-button
        size="small"
        type="primary"
        @click="goToRecommmend()"
        v-if="$store.state.account.nowRole.type==3"
      ></el-button>
      <el-button
        size="small"
        type="primary"
        @click="distributionWorks()"
        v-if="$store.state.account.nowRole.type!=3"
      ></el-button>
      <el-button size="small" @click="downloadWorks()"></el-button>
      <el-button size="small" @click="exportWorks()">excel</el-button>
      <el-button
        size="small"
        type="danger"
        @click="returnWorks()"
        plain
        v-if="$store.state.account.nowRole.type==3"
      ></el-button>
    </el-row>
    <div style="margin-top:16px;">
      <template>
        <!--       -->
        <el-table
          :data="tableData"
          ref="multipleTable"
          border
          :header-cell-style="{background:'#EEEEEE',color:'#323232'}"
          tooltip-effect="dark"
          style="width: 100%"
          @selection-change="tableSelectionChange"
        >
          >
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column label="" type="index" width="80" align="center"></el-table-column>
          <el-table-column prop="works.title" label="" show-overflow-tooltip></el-table-column>
          <el-table-column prop="author_" label=""></el-table-column>
          <el-table-column prop="mentor_" label=""></el-table-column>
          <el-table-column prop="areaName" label="" show-overflow-tooltip></el-table-column>
          <el-table-column prop="title_" label="" show-overflow-tooltip></el-table-column>
          <el-table-column prop="info.category" label="" show-overflow-tooltip></el-table-column>
          <el-table-column prop="info.project" label="" show-overflow-tooltip></el-table-column>
          <el-table-column
            label=""
            show-overflow-tooltip
            v-if="$store.state.account.nowRole.type!=3"
          >
            <template slot-scope="scope">
              <template
                v-if="scope.row.professional.length>0"
                v-for="item in scope.row.professional"
              >{{item.name }}</template>
              <template v-else></template>
            </template>
          </el-table-column>
          <el-table-column
            width="160"
            label=""
            show-overflow-tooltip
            v-if="$store.state.account.nowRole.type==3"
          >
            <template slot-scope="scope">
              <template v-if="scope.row.position==0">
                <el-button type="text" @click="goToRecommmendItem(scope.row.id)"></el-button>
              </template>
              <template v-else>
                
                <el-button type="text" style="color:red;" @click="goToDisRecommmendItem(scope.row.id)"></el-button>
              </template>
            </template>
          </el-table-column>
        </el-table>
      </template>
    </div>

    <el-row class="page-div">
      <el-pagination
        @size-change="pageSizeChange"
        @current-change="pageCurrentChange"
        :current-page="currentPage"
        :page-sizes="[20, 30, 40, 50, 100]"
        :page-size="perPage"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalCount"
      ></el-pagination>
    </el-row>

    <div v-if="dialogVisible" class="mask">
      <div class="box">
        <my-transfer @edit="edit" @Noedit="Noedit" :workList="multipleSelection"></my-transfer>
      </div>
    </div>
  </div>
</template>
<script>
import MyTransfer from "../../components/MyTransfer";
import TypeSelect from "../../components/TypeSelect";
import api from "../../service/api";
import { axiosGet, getCookie } from "../../tools/tools";
import axios from "axios";
export default {
  components: { MyTransfer, TypeSelect },
  props: {
    id: {
      type: Number,
      default() {
        return 0;
      }
    }
  },
  data() {
    return {
      gradeList: [
        { id: 0, name: "" },
        { name: "", id: 1 },
        { name: "", id: 2 },
        { name: "", id: 4 },
        { name: "", id: 8 },
        { name: "", id: 16 },
        { name: "", id: 32 },
        { name: "", id: 64 }
      ], //
      activityProjectList: [{ id: 0, name: "" }], //
      activityTypleList: [{ id: 0, name: "" }], //
      regionList: [{ id: 0, name: "" }], //
      multipleSelection: [], //
      form: {
        school: "",
        work_title: "",
        mentor: "",
        author: ""
      },
      tableData: [],
      dialogVisible: false,
      totalCount: 0, //
      perPage: 20,
      currentPage: 1,
      region: "", //
      item_id: "", //
      period: "", //
      category_id: "", //
      ids: [] //id
    };
  },
  watch: {
    id: function(val) {
      console.log(val, "id");
    }
  },
  created() {
    let params = {};
    this.getEnrollList(params);
  },
  mounted() {
    console.log("id===", this.id);
    this.getActivityDetail();
  },
  methods: {
    //
    async getActivityDetail() {
      let params = {};
      params.url = api.activity;
      params.id = this.id;
      params.expand =
        "detail,region,node,attachment,banner,category,categoryDetail";
      await axiosGet(params)
        .then(res => {
          console.log(res.categoryDetail);
          if (res.categoryDetail.length > 0) {
            res.categoryDetail.forEach(element => {
              this.activityTypleList.push({
                id: element.id,
                name: element.title
              });
              element.child.forEach(ite => {
                this.activityProjectList.push({ id: ite.id, name: ite.title });
              });
            });
            res.region.forEach(item => {
              this.regionList.push({ id: item.id, name: item.area_name });
            });
            console.log(this.activityTypleList);
          }
        })
        .catch(err => err);
    },
    //
    async gradeObject(val) {
      console.log(val);
      this.period = val;
      let params = {
        "per-page": this.perPage
      };
      params.page = this.currentPage;
      this.getEnrollList(params);
    },
    //
    async activityProjectObject(val) {
      console.log(val);
      this.item_id = val;
      let params = {
        "per-page": this.perPage
      };
      params.page = this.currentPage;
      this.getEnrollList(params);
    },
    //
    async activityTypleObject(val) {
      console.log(val);
      this.category_id = val;
      let params = {
        "per-page": this.perPage
      };
      params.page = this.currentPage;
      this.getEnrollList(params);
    },
    //
    async regionObject(val) {
      console.log(val);
      this.region = val;
      let params = {
        "per-page": this.perPage
      };
      params.page = this.currentPage;
      this.getEnrollList(params);
    },

    //
    tableSelectionChange(val) {
      this.multipleSelection = val;
    },
    //
    goToSearch() {
      let params = {
        "per-page": this.perPage
      };
      params.page = this.currentPage;
      this.getEnrollList(params);
    },
    //
    async downloadWorks() {
      let params = {};
      var ids = [];
      this.multipleSelection.forEach(item => {
        ids.push(item.id);
      });
      axios.get(api.enroll, {
        params: {
          id: ids.join(","),
          bdownload: "1",
          expand:'works'
        },
        headers: {"x-api-key" : getCookie("x-api-key") }
      }).then(res=>{
        if(res.data.code==0){
          window.open(res.data.url)
        }
      });
    },
    //
    async exportWorks() {
       let params = {};
      var ids = [];
      this.multipleSelection.forEach(item => {
        ids.push(item.id);
      });
      axios.get(api.enroll, {
        params: {
          id: ids.join(","),
          bexport: "1",
          expand:'info,works,school,professional,award'
        },
        headers: {"x-api-key" : getCookie("x-api-key") }
      }).then(res=>{
        console.log("res===",res)
      });
    },
    //
    async returnWorks() {
      this.multipleSelection.forEach(item => {
        this.ids.push(item.id);
      });
      let params = {};
      params.url = api.reject;
      params.ids = this.ids;
      await this.axiosPost(params)
        .then(res => {
          if (res.code == 0) {
            this.$message({
              type: "success",
              message: ""
            });
            let params = {
              "per-page": this.perPage
            };
            params.page = this.currentPage;
            this.getEnrollList(params);
          } else if (res.code == -1) {
            this.$message({
              type: "warning",
              message: res.message
            });
          }
        })
        .catch(err => err);
    },
    //
    async goToRecommmend(){
      this.ids=[]
      this.multipleSelection.forEach(item => {
        this.ids.push(item.id);
      });
      let params = {};
      params.url = api.recommend;
      params.ids = this.ids;
      params.position=1
      params.value=1
      await this.axiosPost(params).then(res=>{
        if (res.code == 0) {
            this.$message({
              type: "success",
              message: ""
            });
            let params = {
              "per-page": this.perPage
            };
            params.page = this.currentPage;
            this.getEnrollList(params);
          } else if (res.code == -1) {
            this.$message({
              type: "warning",
              message: res.message
            });
          }
      }).catch(err=>err)
    },
    //
    async goToRecommmendItem(id){
      this.ids=[]
      this.ids.push(id)
      let params = {};
      params.url = api.recommend;
      params.ids = this.ids;
      params.position=1
      params.value=1
      await this.axiosPost(params).then(res=>{
        if (res.code == 0) {
            this.$message({
              type: "success",
              message: ""
            });
            let params = {
              "per-page": this.perPage
            };
            params.page = this.currentPage;
            this.getEnrollList(params);
          } else if (res.code == -1) {
            this.$message({
              type: "warning",
              message: res.message
            });
          }
      }).catch(err=>err)
    },
    //
    async goToDisRecommmendItem(id){
      this.ids=[]
      this.ids.push(id)
      let params = {};
      params.url = api.recommend;
      params.ids = this.ids;
      params.position=1
      params.value=0
      await this.axiosPost(params).then(res=>{
        if (res.code == 0) {
            this.$message({
              type: "success",
              message: ""
            });
            let params = {
              "per-page": this.perPage
            };
            params.page = this.currentPage;
            this.getEnrollList(params);
          } else if (res.code == -1) {
            this.$message({
              type: "warning",
              message: res.message
            });
          }
      }).catch(err=>err)
    },
    //
    async getEnrollList(params) {
      if (this.form.school != "") {
        params.school = this.form.school;
      }
      if (this.form.work_title != "") {
        params.work_title = this.form.work_title;
      }
      if (this.form.mentor != "") {
        params.mentor = this.form.mentor;
      }
      if (this.form.author != "") {
        params.author = this.form.author;
      }
      if (this.period != 0 && this.period != "") {
        params.period = this.period;
      }
      if (this.category_id != 0 && this.category_id != "") {
        params.category_id = this.category_id;
      }
      if (this.item_id != 0 && this.item_id != "") {
        params.item_id = this.item_id;
      }
      if (this.region != 0 && this.region != "77") {
        params.region = this.region;
      }
      if(this.$store.state.account.nowRole.type!=3){
        params.position=1
      }
      params.url = api.enroll;
      params.expand = "info,works,school,professional";
      params.activity_id = this.id;
      await this.axiosGet(params)
        .then(res => {
          this.tableData=[]
          this.tableData = res.items;
          this.tableData.forEach(ite => {
            var title = "";
            var areaName = "";
            var author_ = "";
            var mentor_ = "";
            ite.school.forEach(item => {
              title == ""
                ? (title = item.title)
                : (title = title + "," + item.title);
              areaName = item.areaInfo.areaName;
            });
            ite.works.member.author.forEach(item => {
              author_ == ""
                ? (author_ = item.name)
                : (author_ = author_ + "," + item.name);
            });
            ite.works.member.mentor.forEach(item => {
              mentor_ == ""
                ? (mentor_ = item.name)
                : (mentor = mentor_ + "," + item.name);
            });
            ite.mentor_ = mentor_;
            ite.author_ = author_;
            ite.areaName = areaName;
            ite.title_ = title;
          });
          this.totalCount = res._meta.totalCount;
        })
        .catch(err => err);
    },
    edit(item) {
      this.dialogVisible = item;
      let params = {
        "per-page": this.perPage
      };
      params.page = this.currentPage;
      this.getEnrollList(params);
    },
    Noedit(item) {
      this.dialogVisible = item;
    },
    handleClose(done) {
      this.$confirm("")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    //
    distributionWorks() {
      this.dialogVisible = !this.dialogVisible;
    },
    //
    pageSizeChange(val) {
      this.perPage = val;
      let params = {
        "per-page": this.perPage
      };
      params.page = this.currentPage;
      this.getEnrollList(params);
    },
    //
    pageCurrentChange(val) {
      this.currentPage = val;
      let params = {
        "per-page": this.perPage
      };
      params.page = this.currentPage;
      this.getEnrollList(params);
    }
  }
};
</script>
<style lang="less" scoped>
.page-div {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}
.buttonenum {
  float: left;
  margin: 20px 0 10px 0;
}
.mask {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.6);
  z-index: 3;
}
.box {
  background: #ffffff;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-top: -250px;
  margin-left: -400px;
  width: 782px;
  min-height: 500px;
  background: rgba(255, 255, 255, 1);
  border-radius: 6px;
}
</style>
