<template>
    <div class="work-review-container">
        <el-card style="min-height:600px;"> 
            <el-tabs v-model="workReview">
                <el-tab-pane label="" name="workReview">
                    <el-row>
                        <el-col>
                            <type-select :gradeList="gradeList" :activityNameList="activityNameList" :activityTypleList="activityTypleList" :activityProjectList="activityProjectList" 
							@activityNameObject='activityNameObject' @gradeObject='gradeObject' @activityTypleObject='activityTypleObject' @activityProjectObject="activityProjectObject"></type-select>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="2">
                            <el-button type="primary" plain size="small" @click="downloadWork"></el-button>
                        </el-col>
                        <el-col :span="3">
                            <!-- <el-button type="primary" plain @click="goToImport" size="small"></el-button> -->
                        </el-col>
                        <el-col :span="2">
                            <!-- <el-button type="primary" plain @click="goToDownload" size="small"></el-button> -->
                        </el-col>
                        <el-col :span="8" :offset="11">
                            <el-form :inline="true" :model="formInline" class="demo-form-inline" style="width:900px">
                                <el-form-item label="" >
                                    <el-input v-model="formInline.activityName" placeholder="" style="width:350px" size="small"></el-input>
                                </el-form-item>
                                <el-form-item>
                                    <el-button type="primary" @click="goToSearch" size="small"></el-button>
                                </el-form-item>
                            </el-form>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-table
                            ref="multipleTable"
                            :data="tableData"
                            border
                            :header-cell-style="{background:'#EEEEEE',color:'#323232'}"
                            tooltip-effect="dark"
                            style="width: 100%"
                            v-loading="loading"
                            @selection-change="tableSelectionChange">
                            <el-table-column
                            type="selection"
                            width="55">
                            </el-table-column>
                            <el-table-column
                            label=""
                            type="index"
                            width="100">
                            </el-table-column>

                            <el-table-column
                            prop="activity"
                            label=""
                            show-overflow-tooltip>
                            </el-table-column>

                            <el-table-column
                            prop="category"
                            label=""
                            show-overflow-tooltip>
                            </el-table-column>

                            <el-table-column
                            prop="project"
                            label=""
                            show-overflow-tooltip>
                            </el-table-column>

                            <el-table-column
                            prop="title"
                            label=""
                            show-overflow-tooltip>
                            </el-table-column>
                            
                            <el-table-column
                            label=""
                            prop="score">
                            </el-table-column>

                            <el-table-column
                            prop="comment"
                            label=""
                            show-overflow-tooltip>
                            </el-table-column>
                            <el-table-column
                            fixed="right"
                            label=""
                            width="100">
                            <template slot-scope="scope">
                                <el-button @click="goToLook(scope.row)" v-if="scope.row.score==0&&scope.row.comment==''" type="text" size="small"></el-button>
                                <el-button @click="goToLook(scope.row)" v-else  type="text" size="small" style="color:red"></el-button>

                            </template>
                            </el-table-column>
                        </el-table>
                    </el-row>
                    <el-row class="page-div">
                        <el-pagination
                         @size-change="handleSizeChange"
                         @current-change="handleCurrentChange"
                         :current-page="pages.now_page"
                         :page-sizes="[10, 20, 30, 40]"
                         :page-size="searchParams.per_page"
                         layout="total, sizes, prev, pager, next, jumper"
                         :total="pages.total">
                         </el-pagination>
                    </el-row>
                </el-tab-pane>
            </el-tabs>
        </el-card>
    </div>
</template>
<script>
import TypeSelect from '../components/TypeSelect';
import api from "../service/api";
import { axiosGet, getCookie } from "../tools/tools";
import axios from "axios";
export default {
    components: {TypeSelect},
    data(){
        return{
            workReview:'workReview',
            userid: "", //id
            activityNameList:[
                // {id:0,name:''},
                // {id:1,name:''},
                // {id:2,name:''},
                // {id:3,name:''}
            ],
            activityNameSelectID:'',//id
            activityTypleList:[
                // {id:0,name:''},
                // {id:1,name:''},
                // {id:2,name:''},
                // {id:3,name:''}
            ],
            activityTypleSelectID:'',//id
            activityProjectList:[
                // {id:0,name:''},
                // {id:1,name:''},
                // {id:2,name:''},
                // {id:3,name:''},
                // {id:4,name:''}
            ],
            activityProjectSelectID:'',
            gradeList:[
                { id: 0, name: '' },
                { id: 1, name: '' },
                { id: 2, name: '' },
                { id: 3, name: '' }
            ],
            gradeSelectID:'',//id
            formInline: {
                activityName: '',
                activityType:''
            },
            tableData: [
                // {
                // activityName:'',
                // activityType:'',
                // activityProject:'',
                // score: '59',
                // workName: '',
                // comment:'  '
                // }
            ],
            multipleSelection: [],
            selectedCell:[],//
            pages:{now_page:1,per_page:10,total:0},
            searchParams:{now_page:0,per_page:10},
            apiKey:getCookie("x-api-key"),
            loading:true,
        }
    },
    created() {
        this.userid = JSON.parse(localStorage.getItem("user")).id;
        this.getWorksList();
        this.getActivityList();
    },
    mounted(){

    },
    methods:{
        //
        async getActivityList(){
            let that=this;
            let params={};
            params.url=api.activity;
            params.expand='category';
            params.inscore=1;
            await this.axiosGet(params).then(res=>{
                that.activityNameList=res.items.map(item=>{
                    return {
                        "id":item.id,
                        "name":item.title,
                    }
                })
                that.activityTypleList=res.items.map(item=>{
                    return {
                        "id":item.category[0].id,
                        "name":item.category[0].title,
                    }
                })
                that.activityProjectList=res.items.map(item=>{
                    return {
                        "id":item.category[0].child[0].id,
                        "name":item.category[0].child[0].title,
                    }
                })
            })
            that.activityNameList.unshift({id:0,name:""});
            that.activityTypleList.unshift({id:0,name:""});
            that.activityProjectList.unshift({id:0,name:""});
        },
        //
        async getWorksList(){
            let that=this;
            let params={};
            params.url=api.enroll;
            params.inscore=1;
            params.expand='info,works,professional';
            params.currentPage=that.pages.now_page;
            if(that.pages.per_page!=0){
                params.pagesize=that.pages.per_page;
            }
            if(that.activityNameSelectID!=''){
               params.activity_id=that.activityNameSelectID;//
            }
            if(that.gradeSelectID!=''&&that.gradeSelectID!=0){
               params.period=that.gradeSelectID;// []  
            }
            if(that.activityTypleSelectID!=''&&that.activityTypleSelectID!=0){
               params.category_id=that.activityTypleSelectID;//
            }
            if(that.activityProjectSelectID!=''&&that.activityProjectSelectID!=0){
               params.item_id=that.activityProjectSelectID;//
            }
            if(that.formInline.activityName!=''){//
                params.works_title=that.formInline.activityName;
            }
            //if(that.selectedCell.length>0){
            //    params.bdownload=1;//
            //    params.id=that.selectedCell.join(",");//
            //    console.log(params.id);
            //}
            // params.bself=1,//
            // params.region='',//
            // params.school='',//
            // params.author='',//
            // params.mentor='',//[] 
            // params.exhibit='',//[] 
            // params.bexport=1,//excel
            //params.sort='score',//scorescore -scorescore
            await this.axiosGet(params).then(res=>{
                
                that.tableData=[];
                let item=res.items;
                for(let i=0;i<item.length;i++){
                // console.log("ces",Object.assign(item[i].info,item[i].works));
                    that.tableData.push(Object.assign(item[i].info,item[i].works,item[i].professional));
                }
                for(let i=0;i<that.tableData.length;i++){
                    that.tableData[i].comment=that.tableData[i][0].comment||"";
                    that.tableData[i].score=that.tableData[i][0].score/10||0;
                }
                that.pages.per_page=res._meta.pageCount;
                that.pages.now_page=res._meta.currentPage;
                that.pages.total=res._meta.totalCount;
                that.loading=false;

            }).catch(err=>{
                console.log(err);
                that.loading=false;
            });
        },
        //
        goToSearch(){
            let that=this;
            that.getWorksList();
        },
        //
        goToImport(){
            this.$router.push({
                path:'/importWorkList'
            })
        },
        //
        goToDownload(){

        },
        //
        downloadWork(){
            let that=this;
            axios.get(api.enroll, {
             params: {
               id: that.selectedCell.join(","),
               bdownload: "1",
               inscore:"1"
             },
             headers: { 'x-api-key': that.apiKey }
            }).then(res=>{
                let datas=res.data;
                if(datas.code==0){
                    console.log("",datas.url);
                    window.location.href=datas.url;
                    return;
                }
                that.selectedCell=[];//
            });
        },
        //
        goToLook(row){
            let query;
            this.$router.push({
                path:'/workScore',
                query:{"id":row.id,"score":row.score,"comment":row.comment}
            });

        },
        //
        handleSizeChange(val) {
            let that=this;
            that.searchParams.per_page=val;
            that.getWorksList();
        },
        //
        handleCurrentChange(val) {
            let that=this;
            that.searchParams.now_page=val;
            that.getWorksList();
        },
        //
        open(index,rows) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                rows.splice(index, 1);
                this.$message({
                    type: 'success',
                    message: '!'
                });
                
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });          
            });
        },
        //
        async activityNameObject(value) {
            let that=this;
            that.activityNameSelectID=value;
            that.getWorksList();
        },
        //
        async activityTypleObject(value) {
            let that=this;
            that.activityTypleSelectID=value;
            that.getWorksList();
        },
        //
        async gradeObject(value) {
            let that=this;
            that.gradeSelectID=value;
            that.getWorksList();
        },
        //
        async activityProjectObject(value){
            let that=this;
            that.activityProjectSelectID=value;
            that.getWorksList();
        },
        //
        async tableSelectionChange(val){
            let that=this;
            let select=val;
            that.selectedCell=select.map((item)=>{
                return item.id;
            })
            // console.log("ces",that.selectedCell);
        }
			
    }
}
</script>
<style lang="less" scoped>
.work-review-container{
    width: 1180px;
    margin: auto;
    margin-top: 20px;
    .page-div{
        display:flex;
        justify-content:center;
    }
}
</style>
